import React, { Component } from 'react'
import '@hi-ui/hiui/es/table/style/index.css'
import Form from '@hi-ui/hiui/es/form'
import Input from '@hi-ui/hiui/es/input'
import Button from '@hi-ui/hiui/es/button'
import DatePicker from '@hi-ui/hiui/es/date-picker'
import Counter from '@hi-ui/hiui/es/counter'
import Select from '@hi-ui/hiui/es/select'
import Radio from '@hi-ui/hiui/es/radio'
import Modal from '@hi-ui/hiui/es/modal'
import Grid from '@hi-ui/hiui/es/grid'
import './index.scss'

const FormItem = Form.Item

export default class Template extends Component {
  constructor (props) {
    super(props)
    this.state = {
      forms: this.initForms(),
      smallShow: false,
      bigShow: false
    }
    this.singleList = [
      { name: '', label: '', id: '2' },
      { name: '', label: 'tanke', id: '3' },
      { name: '', label: 'chaojitanke', id: '4', disabled: true },
      { name: '', label: 'wurenji', id: '5' },
      { name: '', label: 'huojian', id: '6' }
    ]
  }

  initForms () {
    return Object.assign(
      {},
      {
        text: '',
        Date: { start: new Date(), end: new Date() },
        num: 0,
        time: new Date(),
        select: '4',
        radio: 2,
        longText: ''
      }
    )
  }

  handleChange () {}

  handleSubmit () {}

  reset () {}

  triggerBig (value) {
    this.setState({ bigShow: value })
  }

  triggerSmall (value) {
    this.setState({ smallShow: value })
  }

  render () {
    const Row = Grid.Row
    const Col = Grid.Col
    const { forms } = this.state

    return (
      <div className='page--form-modal'>
        <h2 className='hi-form__title'></h2>

        <Button type='primary' onClick={this.triggerBig.bind(this, true)}>
          
        </Button>
        <Button type='primary' onClick={this.triggerSmall.bind(this, true)}>
          
        </Button>

        <Modal
          title=''
          show={this.state.bigShow}
          onConfirm={this.triggerBig.bind(this, false)}
          onCancel={this.triggerBig.bind(this, false)}
          width={800}
          footers={[
            <Button type='primary' key={1} onClick={this.handleSubmit.bind(this)}>
              
            </Button>,
            <Button type='default' key={2} onClick={this.reset.bind(this)}>
              
            </Button>
          ]}
        >
          <Form
            ref={this.form1}
            model={forms}
            rules={this.state.rules}
            labelWidth='90'
            labelPosition='right'
          >
            <Row>
              <Col span={14}>
                <FormItem label='label' prop='text'>
                  <Input
                    value={forms.text}
                    placeholder={'name'}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '320px' }}
                  />
                </FormItem>
                <FormItem label='Date' prop='Date'>
                  <DatePicker
                    type='daterange'
                    value={forms.Date}
                    onChange={d => {
                      console.log(d)
                    }}
                  />
                </FormItem>
                <FormItem label='long text' prop='longText'>
                  <Input
                    value={forms.longText}
                    placeholder={''}
                    onChange={this.handleChange.bind(this, 'column1')}
                    style={{ width: '320px', height: '100px' }}
                    type='textarea'
                  />
                </FormItem>
              </Col>
              <Col span={10}>
                <FormItem label='label' prop='select'>
                  <Select
                    list={this.singleList}
                    placeholder=''
                    style={{ width: '200px' }}
                    value={forms.select}
                    onChange={item => {
                      console.log('', item)
                    }}
                  />
                </FormItem>
                <FormItem label='Numer' prop='num'>
                  <Counter
                    value={forms.num}
                    step='1'
                    min='0'
                    max='8'
                    onChange={val => console.log('', val)}
                  />
                </FormItem>
                <FormItem label='Raido' prop='radio'>
                  <Radio
                    list={['', '', '', '', '', '']}
                    checked={forms.radio}
                    onChange={this.handleChange.bind(this, 'region', '')}
                  />
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>

        <Modal
          title=''
          show={this.state.smallShow}
          onConfirm={this.triggerSmall.bind(this, false)}
          onCancel={this.triggerSmall.bind(this, false)}
          width={600}
          footers={[
            <Button type='primary' key={1} onClick={this.handleSubmit.bind(this)}>
              
            </Button>,
            <Button type='default' key={2} onClick={this.reset.bind(this)}>
              
            </Button>
          ]}
        >
          <Form
            ref={this.form1}
            model={forms}
            rules={this.state.rules}
            labelWidth='90'
            labelPosition='right'
          >
            <FormItem label='label' prop='text'>
              <Input
                value={forms.text}
                placeholder={'name'}
                onChange={this.handleChange.bind(this, 'column1')}
                style={{ width: '250px' }}
              />
            </FormItem>
            <FormItem label='Date' prop='Date'>
              <DatePicker
                type='daterange'
                value={forms.Date}
                onChange={d => {
                  console.log(d)
                }}
              />
            </FormItem>
            <FormItem label='Numer' prop='num'>
              <Counter
                value={forms.num}
                step='1'
                min='0'
                max='8'
                onChange={val => console.log('', val)}
              />
            </FormItem>
            <FormItem label='label' prop='select'>
              <Select
                list={this.singleList}
                placeholder=''
                style={{ width: '200px' }}
                value={forms.select}
                onChange={item => {
                  console.log('', item)
                }}
              />
            </FormItem>
          </Form>
        </Modal>
      </div>
    )
  }
}
